export const hexMix = (hexColor1, hexColor2, ratio) => {
	// Convert hex colors to RGB
	const r1 = parseInt(hexColor1.substring(0, 2), 16);
	const g1 = parseInt(hexColor1.substring(2, 4), 16);
	const b1 = parseInt(hexColor1.substring(4, 6), 16);

	const r2 = parseInt(hexColor2.substring(0, 2), 16);
	const g2 = parseInt(hexColor2.substring(2, 4), 16);
	const b2 = parseInt(hexColor2.substring(4, 6), 16);

	// Calculate the ratio between the two colors
	const ratioFloat = ratio / 100;
	const invRatioFloat = 1 - ratioFloat;

	// Calculate the resulting color
	const r = Math.round(r1 * invRatioFloat + r2 * ratioFloat);
	const g = Math.round(g1 * invRatioFloat + g2 * ratioFloat);
	const b = Math.round(b1 * invRatioFloat + b2 * ratioFloat);

	// Convert the resulting RGB color to hexadecimal format
	const hexColor = ((r << 16) + (g << 8) + b).toString(16).padStart(6, '0');
	return '#' + hexColor;
};
