import { Canvas, applyProps, useFrame } from '@react-three/fiber';
import { debounce } from './utils/debounce';
import { LayerMaterial, Color, Depth } from 'lamina';
import { PerformanceMonitor, AccumulativeShadows, RandomizedLight, Environment, Lightformer, Float, useGLTF, OrbitControls } from '@react-three/drei';
import { useEffect, useLayoutEffect, useRef, useState } from 'react';
import * as THREE from 'three';
import { hexMix } from './utils/hexMix';

const viewport = {
	height: window.innerHeight,
	width: window.innerWidth
};

function sinusoidalFunction(elapsedTime) {
  const period = 9000; // 3 seconds in milliseconds
  const amplitude = 50; // half of the range, so the values oscillate between 0 and 100
  const phaseShift = Math.PI / 2; // 90-degree phase shift

  const angularFrequency = (2 * Math.PI) / period;
  const phase = angularFrequency * elapsedTime + phaseShift;

  const value = Math.sin(phase) * amplitude + amplitude;

  return value;
}


const renderer = new THREE.WebGLRenderer({ antialias: true });
renderer.outputEncoding = THREE.sRGBEncoding;
renderer.physicallyCorrectLights = true;
renderer.shadowMap.enabled = true;
renderer.shadowMap.type = THREE.PCFSoftShadowMap;

export const App = () => {
  const [devicePixelRatio, setDevicePixelRatio] = useState(2);
  const [lowPerformance, setLowPerformance] = useState(false);

  useEffect(() => {
    const debouncedHandleResize = debounce(() => {
      viewport.height = window.innerHeight,
      viewport.width = window.innerWidth;
      renderer.setSize(viewport.width, viewport.height);
      setDevicePixelRatio(Math.min(2, window.devicePixelRatio));
    }, 1000);

    window.addEventListener('resize', debouncedHandleResize);

    return () => {
      window.removeEventListener('resize', debouncedHandleResize);
}});

return (
  <Canvas shadows camera={{ position: [5, 3, 15], fov: 30 }} dpr={devicePixelRatio}>
    <spotLight position={[0, 15, 0]} angle={0.3} penumbra={1} castShadow intensity={0.2} shadow-bias={-0.0001} />
    <ambientLight intensity={0.5} />
    <MainPiece />
    <AccumulativeShadows position={[0, -1.16, 0]} frames={64} alphaTest={0.89} scale={10}>
      <RandomizedLight amount={8} radius={10} ambient={0.4} position={[1, 5, -1]} />
    </AccumulativeShadows>
    <PerformanceMonitor onDecline={() => setLowPerformance(true)} />
    <Environment frames={lowPerformance ? 1 : Infinity} resolution={256} background blur={1}>
      <Lightformers />
    </Environment>
  <OrbitControls maxDistance={45} minDistance={4}
  maxPolarAngle={1.7} 
  minPolarAngle={ 0.5} enablePan={false} autoRotate autoRotateSpeed={0.35} target={[0,1,0]} enableDamping dampingFactor={0.01}/>
  </Canvas>
  )
}

const MainPiece = (props) => {
  const { scene, nodes, materials } = useGLTF('/poop_full_res.glb')
  scene.children.forEach(mesh => {
    mesh.scale.set(0.15, 0.15, 0.15);
    mesh.rotation.z = 0.75*Math.PI;
    mesh.position.y = 1.33;
    mesh.position.x = -0.25;
    mesh.position.z = 0.5;

  });
  useLayoutEffect(() => {
    Object.values(nodes).forEach((node) => node.isMesh && (node.receiveShadow = node.castShadow = true))
    applyProps(materials.Custom, { color: '#222', roughness: 0.0, clearcoat: 0.1 })
    applyProps(materials.Cafe, { color: `#5C4033`, roughness: 0.15, clearcoat: 1 })
  }, [nodes, materials])
  return <primitive object={scene} {...props} />
}

const Lightformers = ({ positions = [2, 0, 2, 0, 2, 0, 2, 0] })  => {
  const group = useRef();
  const total = useRef(0)
  const [c11, setC11] = useState("#2B32CB");
  // const color = useRef("#2B32CB");
  useFrame((_, elapsed) => {
    // total.current += elapsed * 1000;
    // total.current += elapsed * 1000;
    // const ratio =  sinusoidalFunction(total.current);
    // color.current = hexMix("2B32CB","E45B5B", ratio);
    // console.log(color.current);
    // console.log(ratio);
    // console.log(total.current);


    total.current += elapsed * 1000;
    total.current += elapsed * 1000;
    const ratio =  sinusoidalFunction(total.current);
    setC11(hexMix("2B32CB","E45B5B", ratio));
    (group.current.position.z += elapsed * 5) > 20 && (group.current.position.z = -45)
  });

  return (
    <>
      <Lightformer intensity={1} rotation-x={-Math.PI / 2} position={[0, 5, -9]} scale={[10, 10, 1]} />
      <group >
        <group ref={group}>
          {positions.map((x, i) => (
            <Lightformer key={i} form="circle" intensity={2} rotation={[Math.PI / 2, 0, 0]} position={[x, 5, i * 5]} scale={[3, 1, 1]} />
          ))}
        </group>
      </group>
      {/* Accent (red) */}
        <Lightformer form="circle" color="#E45B5B" intensity={1} scale={10} position={[-15, 4, -18]} target={[0, 0, 0]} />
      {/* Background */}
      <mesh scale={100}>
        <sphereGeometry args={[1, 64, 64]} />
        <LayerMaterial side={THREE.BackSide}>
          <Color color={c11} alpha={1} intensity={2} mode="normal" />
        </LayerMaterial>
      </mesh>
    </>
  )
}
